.event {
	padding-bottom: 400px;
	&__bg {
		position: relative;
		padding-bottom: 470px;
		overflow: hidden;
		background-position: 50% 0;
		background-size: cover;
		background-repeat: no-repeat;
		&:before,
		&:after {
			position: absolute;
			content: "";
			width: 100vw;
			height: 50vh;
			background-color: $white; }
		&:before {
			top: auto;
			bottom: 0;
			left: 50%;
			transform-origin: left bottom;
			@include rotate(-172); }
		&:after {
			top: 100%;
			right: -50%;
			transform-origin: top left;
			@include rotate(-8); } }
	&__row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -8px 16px; }
	&__col {
		padding: 0 8px; }
	&__title-lg {
		text-align: center;
		margin-top: 57px;
		margin-bottom: 38px; }
	&__description {
		font-weight: 700;
		line-height: 2.06;
		color: $lightBlue;
		margin-bottom: 50px; }
	&__description p:not(:last-child) {
		margin-bottom: 35px; }
	&__share {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 35px 0;
		box-shadow: inset 0 2px 0 0 #f2f2f2, inset 0 -2px 0 0 #f2f2f2;
		margin-bottom: 80px; }
	&__subtitle {
		margin-right: 28px;
		color: $lightBlue;
		font-weight: 700; }
	&__soc-icons {
		display: flex;
		align-items: center; }
	&__soc:not(:last-child) {
		margin-right: 14px; } }

.img-lg {
	display: block;
	padding-bottom: 78.2%;
	background-size: cover;
	background-position: 50% 50%;
	border-radius: 10px;
	@include sm {
		padding-bottom: 100%; } }

.img-md {
	display: block;
	padding-bottom: 37.5%;
	background-size: cover;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	border-radius: 10px;
	@include sm {
		padding-bottom: 100%; } }

.img-sm {
	display: block;
	padding-bottom: 77.42%;
	background-size: cover;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	border-radius: 10px;
	@include sm {
		padding-bottom: 100%; } }

.video {
	box-shadow: 0 42px 40px -20px rgba(107, 162, 255, 0.1);
	width: 100%;
	// height: 400px
	position: relative;
	overflow: hidden;
	border-radius: 8px;
	video {
		width: 100%; }
	&__play {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
		.is-play & {
			opacity: 0;
			visibility: visible; } }
	&__content {
		padding: 30px 0 15px;
		text-align: center; }
	&__name {
		font-weight: 700;
		color: $darkBlue; }
	&__description {
		font-weight: 500;
		color: $lightBlue; } }



