.hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-position: 50% 0;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__bg:before, .hero__bg:after {
  position: absolute;
  content: "";
  width: 100vw;
  height: 50vh;
  background-color: #fff;
}

.hero__bg:before {
  top: auto;
  bottom: 0;
  left: 50%;
  transform-origin: left bottom;
  transform: rotate(-172deg);
}

.hero__bg:after {
  top: 100%;
  right: -50%;
