  transition: background-color .3s ease-in-out, transform .3s ease-in-out;
  display: inline-block;
}

.slider .slick-dots button:before {
  position: absolute;
  content: "";
  top: -10px;
  left: -4px;
  right: -4px;
  bottom: -10px;
}

